.search {
  position: absolute;
  right: 20px;
  width: 30vw;
  min-width: 180px;
}

.search input {
  padding-left: 15px;
  border: 2px outset #b0e5ff40;
  width: 100%;
  height: 40px;
  border-radius: 999px;
  background-color: #ffffff15;
  backdrop-filter: blur(10px);
  color: white;
  font-size: 1.1rem;
}

::placeholder {
  color: white;
  font-style: italic;
}

.search input:active, .search input:focus{
  outline: none
}

.button {
    position: absolute;
    /* ↓ mi intento (en vano) por colocar el ícono en el centro del botón :face-palm: */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    right: 5px;
    top: 5px;
    bottom: 5px;
    border: 2px outset #b0e5ff80;
    height: 30px;
    width: 30px;
    border-radius: 999px;
    /* font-size: 30px; */
    /* font-weight: 700; */
    color: #0d455768;

    background-color: transparent;
    transition: background-color 2s ease-out;
}

.button:hover {
    background-color: #38a9d233;
    transition: background-color 0.2s;
}

.button:active, button:focus{
    outline: none
}